.App {
  text-align: center;
  font-family: Arial, sans-serif;
  background-color: #f0f4f8;
  color: #333;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-header {
  background-color: #fff;
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  width: 90%;
}

.controls {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-top: 20px;
}

input[type="file"] {
  border: 2px dashed #007bff;
  padding: 15px;
  border-radius: 10px;
  color: #007bff;
  cursor: pointer;
}

.location-btn, .analyze-btn {
  padding: 12px 25px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.location-btn {
  background-color: #28a745;
  color: white;
}

.location-btn:hover {
  background-color: #218838;
}

.analyze-btn {
  background-color: #007bff;
  color: white;
}

.analyze-btn:disabled {
  background-color: #a0c4ff;
  cursor: not-allowed;
}

.analyze-btn:hover:enabled {
  background-color: #0056b3;
}

.results-box {
  margin-top: 30px;
  padding: 20px;
  background-color: #e9ecef;
  border-radius: 10px;
  text-align: left;
  border-left: 5px solid #007bff;
}

.results-box h2 {
  margin-top: 0;
  color: #0056b3;
}

.error-message {
  color: #dc3545;
  margin-top: 15px;
  font-weight: bold;
}

.location-info {
  margin-top: 15px;
  font-size: 0.9em;
  color: #666;
}

.recommendation {
  font-style: italic;
  color: #555;
}